/* modules */
.mfp-bg {
  opacity: 1;
  background-color: rgba($color-black, 0.4);
  position: fixed !important; }

/* popup */
.popup {
  position: relative;
  background: $color-white;
  padding: 26px;
  width: auto;
  max-width: 280px;
  margin: 20px auto;
  border-radius: 4px;
  &__title {
    text-transform: uppercase;
    color: $color-red;
    text-align: center;
    font-size: rem(22);
    font-family: $font-roboto-bold;
    line-height: 1;
    margin-bottom: 22px; }
  &--location {
    max-width: 750px;
    padding-left: 50px;
    padding-right: 50px; }
  &__close {
    cursor: pointer;
    position: absolute;
    right: 15px;
    top: -15px;
    width: 31px;
    height: 32px;
    background: $color-red;
    color: $color-white;
    transform: skewX(-20deg);
    border-radius: 3px;
    line-height: 32px;
    padding-left: 9px;
    font-size: rem(16);
    transition: background 0.35s;
    i {
      line-height: 1;
      display: inline-block;
      transform: skewX(20deg);
      margin-left: 2px; }
    &:hover {
      background: $color-lightred; } }
  &__info {
    color: $color-white;
    background: $color-red;
    font-size: rem(13);
    font-family: $font-roboto-bold;
    padding: 9px 10px;
    line-height: 1.2;
    text-align: center;
    &--location {
      margin: 0 -50px 25px -50px; }
    &--pay-services {
      background: $color-lightgray;
      color: $primary-color;
      margin: 20px -26px 20px -26px;
      font-size: rem(15);
      font-family: $font-roboto;
      padding-top: 16px;
      padding-bottom: 16px;
      strong {
        font-family: $font-roboto-bold; } } }
  &__search {
    margin-bottom: 25px;
    &-wrapper {
      display: table;
      width: 100%;
      &>div {
        display: table-cell;
        vertical-align: top;
        width: 100%;
        &:last-child {
          width: 1px; } }
      .input {
        transform: skewX(-15deg);
        border: 1px solid $color-lightgray;
        background: $color-lightgray;
        border-radius: 3px;
        position: relative;
        overflow: hidden;
        display: block;
        input {
          transform: skewX(15deg);
          background: none;
          height: 33px;
          border: 0;
          padding: 0 20px 0 20px;
          width: 100%;
          font-size: rem(12);
          font-family: $font-roboto; } }
      &.is-focus {
        .input {
          background: transparent;
          border-color: #e0e0e0;
          label {
            opacity: 0;
            visibility: hidden; } } } } }
  &__city {
    font-family: $font-roboto;
    ul {
      margin: 0;
      padding: 0;
      li {
        margin: 0;
        list-style: none;
        display: inline-block;
        width: 24%;
        &>a, &>span {
          display: inline-block;
          text-decoration: none;
          color: $primary-color;
          white-space: nowrap;
          line-height: 24px; }
        &>a {
          &:hover {
            color: $color-red; } } } } }
  &__form-bottom {
    background: $color-lightgray;
    text-align: center;
    font-size: rem(13);
    height: 72px;
    margin: 24px -26px -26px -26px;
    border-radius: 0 0 4px 4px;
    padding-top: 18px;
    strong {
      font-size: rem(14);
      text-transform: uppercase;
      // font-family: $font-roboto
      display: block;
      line-height: 1;
      margin-bottom: 5px; } }
  &__wrapper {
    .btn {
      margin-bottom: rem(13); } }
  &__social {
    &-head {
      display: table;
      width: 100%;
      margin-bottom: 12px;
      &>* {
        display: table-cell;
        vertical-align: middle; }
      span {
        padding: 0 5px;
        white-space: nowrap;
        color: $color-red;
        width: 1px; }
      &::after, &::before {
        display: table-cell;
        content: '';
        background-image: linear-gradient(#e5e5e5, #e5e5e5);
        background-size: 100% 1px;
        background-repeat: no-repeat;
        background-position: center;
 } }        // width: 100%
    &-body {
      text-align: center;
      p {
        font-size: rem(12);
        margin-bottom: 12px; }
      a {
        display: inline-block;
        margin: 0 4px;
        opacity: 1;
        &:nth-of-type(1) {
          margin-left: 0; }
        &:last-child {
          margin-right: 0; }
        &:hover {
          opacity: 0.7; } } } }
  &__register-q1, &__register-q2 {
    position: absolute;
    color: $color-white;
    font-size: rem(13);
    font-family: $font-roboto-bold;
    bottom: 7px;
    font-style: normal;
    text-align: center;
    text-transform: uppercase;
    padding-top: 46px;
    b {
      font-size: rem(18); }
    span {
      display: block;
      line-height: 1.2; } }
  &__register-q1 {
    left: -130px; }
  &__register-q2 {
    right: -130px; }
  &__respond {
    &-title {
      font-family: $font-roboto-bold;
      font-size: rem(15);
      margin-bottom: 5px; }
    &-category {
      position: relative;
      padding-right: 25px;
      font-style: italic;
      font-size: rem(13);
      margin-bottom: 5px;
      i {
        top: 2px;
        line-height: 1;
        font-size: rem(15);
        position: absolute;
        right: 0;
        color: $color-red; } }
    &-link {
      font-size: rem(11);
      font-family: $font-roboto-medium;
      margin-bottom: 15px; }
    &-small {
      font-size: rem(10);
      color: $color-808080; } }
  &__pay-services {
    &-title {
      font-family: $font-roboto-bold;
      font-size: rem(15);
      margin-bottom: 5px; }
    &-category {
      position: relative;
      padding-right: 25px;
      font-style: italic;
      font-size: rem(13);
      margin-bottom: 5px;
      i {
        top: 2px;
        line-height: 1;
        font-size: rem(15);
        position: absolute;
        right: 0;
        color: $color-red; } }
    &-link {
      font-size: rem(11);
      font-family: $font-roboto-medium;
      margin-bottom: 15px; }
    &-small {
      font-size: rem(10);
      color: $color-808080; } } }

.mfp-bg-register {
  background-image: url('../img/popup-register-background.png');
  background-position: center center;
  background-repeat: no-repeat; }

/* slide */
.slide {
  background: url('../img/slide-background.png') top center no-repeat;
  background-size: cover;
  height: 390px;
  overflow: hidden;
  &__content {
    float: left;
    // overflow: hidden
    height: 390px;
    margin-left: -13px;
    padding-left: 13px;
    // width: 710px
    width: 100%;
    position: relative;
    z-index: 25;
    margin-top: -369px;
    img {
      // margin-top: -76px
      margin-left: -13px; }
    &-text {
      position: absolute;
      left: 13px;
      bottom: 14px;
      font-family: $font-roboto-bold;
      text-transform: uppercase;
      z-index: 50;
      span {
        display: inline-block;
        border-radius: 4px;
        transform: skewX(-20deg);
        text-align: center;
        position: relative;
        i {
          font-style: normal;
          font-family: $font-roboto-bold;
          transform: skewX(20deg);
          display: inline-block; } }
      .span1 {
        color: $primary-color;
        font-size: rem(12);
        height: 23px;
        line-height: 23px;
        width: 242px;
        background: $color-white;
        z-index: 15;
        margin-left: 80px;
        border-radius: 2px; }
      .span2 {
        font-size: rem(23);
        color: $color-white;
        height: 48px;
        line-height: 48px;
        background: $color-red;
        width: 514px;
        z-index: 10;
        margin-top: -4px; }
      .span3 {
        color: $color-white;
        font-size: rem(39);
        height: 66px;
        line-height: 79px;
        background: $color-blue;
        width: 458px;
        z-index: 5;
        margin-top: -12px;
        margin-left: 12px;
        transform: skewX(-17deg);
        i {
          transform: skewX(17deg); } } }
    &--employer {
      img {
        // margin-top: -13px
        margin-left: -132px; } } }
  &__form {
    float: right;
    width: 360px;
    border-radius: 5px;
    overflow: hidden;
    background: rgba($color-black,.5);
    color: $color-white;
    font-size: rem(12);
    padding: 16px 18px 16px 18px;
    height: 345px;
    margin-top: 24px;
    position: relative;
    z-index: 35;
    legend {
      text-transform: uppercase;
      font-size: rem(20);
      text-align: center;
      color: $color-white; }
    .row {
      &--slide-form {
        margin-left: -5px;
        margin-right: -5px;
        &>div {
          padding: 0 5px; } } }
    .btn {
      &+p {
        margin: 10px 0 10px 0;
        line-height: 16px; } }
    .form {
      &-control, &__control {
        // font-family: $font-roboto
        padding-left: 12px;
        padding-right: 12px;
        &::-webkit-input-placeholder {
          color: $color-808080; }
        &::-moz-placeholder {
          color: $color-808080; }
        &:-ms-input-placeholder {
          color: $color-808080; }
        &:-moz-placeholder {
          color: $color-808080; } }
      &-group, &__group {
        background-color: $color-lightgray;
        border-color: $color-lightgray;
        &.has-error {
          border-color: $color-lightgray;
          &::after {
            content: '';
            background: $color-blue;
            border-radius: 0 3px 3px 0;
            position: absolute;
            top: 0;
            right: -1px;
            top: -1px;
            bottom: -1px;
            width: 38px;
            z-index: 5; } }
        i.ion-alert-circled {
          color: $color-white; }
        &--checkbox, &--radio {
          background: none;
          input[type="checkbox"], input[type="radio"] {
            &~i {
              color: $color-white !important; } }
          .label {
            top: 2px;
            position: relative; } } } } } }

