 }  // -webkit-tap-highlight-color: $selection-color

// Basic Styles
html {
  font-size: inherit;
  line-height: 1.3;
  font-size: 16px; }

html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  min-width: 320px; }

body {
  background: $color-white;
  font-size: rem(13);
  font-family: $font-roboto;
  color: $primary-color;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  &.overflow-hidden {
    overflow: hidden; } }

//  Typography
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  color: $primary-color;
  font-weight: 400;
  text-decoration: none;
  margin: 0 0 15px 0;
  font-family: $font-roboto-bold; }

.h1, h1 {
  font-size: rem(30);
  line-height: 1.2;
  letter-spacing: -1px;
  margin-bottom: 15px;
  text-transform: uppercase; }

.content__head {
  .h1, h1 {
    letter-spacing: 3.5px;
    .ion-checkmark {
      font-size: rem(20);
      margin-left: 10px;
      color: $color-blue;
      vertical-align: 4px; } } }

.h2, h2 {
  font-size: rem(22);
  line-height: 1.14;
  margin-bottom: 30px;
  text-transform: uppercase;
  font-family: $font-roboto-bold;
  letter-spacing: 2px; }


.h3, h3 {
  font-size: rem(18);
  line-height: 1.38;
  font-family: $font-roboto-medium; }

.h4, h4 {
  font-size: rem(14);
  line-height: 1.38;
  font-family: $font-roboto-medium; }

.h5, h5 {
  font-size: rem(13);
  font-family: $font-roboto-medium; }

.h6, h6 {
  font-size: rem(12);
  font-family: $font-roboto-medium; }

.small, small {
  font-size: rem(12);
  line-height: 1.2; }

p {
  line-height: 21px;
  margin: 0 0 20px 0; }

em, i, .em, .i {
  font-style: italic; }

strong, b, .strong, .b {
  font-weight: 400;
  font-family: $font-roboto-bold; }

hr {
  border: solid #ddd;
  border-width: 1px 0 0;
  clear: both;
  margin: 10px 0 30px;
  height: 0; }

// Links
a, .link {
  color: $link-color;
  text-decoration: none;
  outline: none;
  transition: color 0.35s, background-color 0.35s, border-color 0.35s;

  &:hover, &:focus, &:active {
    color: $link-color;
    text-decoration: underline;
    outline: none; } }

a.link {
  &:hover, &:focus, &:active {
    text-decoration: underline; }

  &-dotted {
    text-decoration: none;
    border-bottom: 1px dashed $color-red;
    // display: inline-block
    &:hover, &:focus {
      text-decoration: none;
      border-bottom-color: transparent; } }

  &--underline {
    text-decoration: underline;
    &:hover, &:focus {
      text-decoration: none; } } }

.container {
  margin: 0 auto;
  // width: 1280px
  width: 100%;
  min-width: 320px;
  padding: 0 15px;
  .section {
    min-width: auto; } }


/* text align */
.text {
  &-center, &--center {
    text-align: center; }
  &-left, &--left {
    text-align: left; }
  &-right, &--right {
    text-align: right; } }

/* loader */
.loader {
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 10000;
  background-color: $color-white;
  &__block {
    background: $color-blue;
    animation: load1 1s infinite ease-in-out;
    width: 1em;
    height: 4em;
    position: absolute;
    top: 50%;
    left: 50%;
    color: $color-blue;
    text-indent: -9999em;
    font-size: 11px;
    animation-delay: -0.16s;
    transform: translate(-50%,-50%);
    &:before, &:after {
      background: $color-blue;
      animation: load1 1s infinite ease-in-out;
      width: 1em;
      height: 4em; }
    &:before, &:after {
      position: absolute;
      top: 0;
      content: ''; }
    &:before {
      left: -1.5em;
      animation-delay: -0.32s; }
    &:after {
      left: 1.5em; } }
  @-webkit-keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em; }
    40% {
      box-shadow: 0 -2em;
      height: 5em; } }
  @keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em; }
    40% {
      box-shadow: 0 -2em;
      height: 5em; } } }
/* img */
.img {
  &-responsive, &--responsive {
    display: block;
    height: auto;
    max-width: 100%; }

  &-round, &--round {
    border-radius: 50%; } }

/* section */
.section {
  padding: 50px 0;
  min-width: 320px; }

.content {
  &--all {
    min-height: 552px;
    padding: 50px 0;
    min-width: 320px; } }

ul li {
  list-style-type: disc; }
ol li {
  list-style-type: decimal; }

/* bg */
.bg {
  &--red {
    background-color: $color-red !important; }
  &--white {
    background-color: $color-white !important; }
  &--black {
    background-color: $color-black !important; } }

/* no padding no margin */
.no {
  &--p {
    padding: 0 !important;
    &-top {
      padding-top: 0 !important; }
    &-bottom {
      padding-bottom: 0 !important; }
    &-left {
      padding-left: 0 !important; }
    &-right {
      padding-right: 0 !important; } }

  &--m {
    margin: 0 !important;
    &-top {
      margin-top: 0 !important; }
    &-bottom {
      margin-bottom: 0 !important; }
    &-left {
      margin-left: 0 !important; }
    &-right {
      margin-right: 0 !important; } }

  &--border {
    border: 0 !important; } }

/* font */
.font {
  &--normal {
    font-family: $font-roboto !important; }
  &--bold {
    font-family: $font-roboto-bold !important; }
  &--medium {
    font-family: $font-roboto-medium !important; } }

