/* forms */

input, textarea {
  &::-webkit-input-placeholder {
    color: $color-gray; }
  &::-moz-placeholder {
    color: $color-gray; }
  &:-ms-input-placeholder {
    color: $color-gray; }
  &:-moz-placeholder {
    color: $color-gray; } }

button, input, optgroup, select, textarea {
  margin: 0;
  font: inherit;
  color: inherit;
  outline: none;
  &:hover, &:focus, &:active {
    outline: none; } }

button, html input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
  outline: none; }

.form {
  &-group, &__group {
    margin: 0 4px 14px 4px;
    transform: skewX(-15deg);
    border: 1px solid $color-lightgray;
    background: $color-lightgray;
    border-radius: 4px;
    position: relative;
    // overflow: hidden
    display: block;
    &.is-focus {
      background: $color-white;
      border-color: #e0e0e0; }
    &--checkbox, &--radio {
      background: none;
      transform: skewX(0);
      border: 0;
      position: relative;
      // padding-left: 23px
      font-size: rem(12);
      margin-bottom: 7px;
      label {
        margin: 0; }
      .label {
        cursor: pointer;
        padding-left: 23px;
        position: relative;
        z-index: 1;
        .small {
          display: block;
          color: $color-808080;
          font-size: rem(11);
          line-height: 16px;
          margin-top: 2px;
          font-family: $font-roboto; } }
      &.form-group--small {
        font-size: rem(10); }
      input[type="checkbox"], input[type="radio"] {
        display: none;
        &~i.ion-android-checkbox-outline, &~i.ion-android-checkmark-circle {
          display: none; }
        &~i.ion-android-checkbox-outline-blank, &~i.ion-android-radio-button-off {
          display: block; }
        &:checked {
          &~i.ion-android-checkbox-outline, &~i.ion-android-checkmark-circle {
            display: block; }
          &~i.ion-android-checkbox-outline-blank, &~i.ion-android-radio-button-off {
            display: none; }
          &~i[class*='ion-android'] {
            color: $color-red; } } }
      input[type="radio"] {
        &:checked {
          &~label {
            color: $color-red; } } }

      i[class*='ion-android'] {
        position: absolute;
        left: 0;
        top: 0;
        color: #969696;
        font-size: rem(16);
        line-height: 1;
        display: block; }
      &-medium {
        font-size: rem(14);
        font-family: $font-roboto-medium;
        i[class*='ion-android'] {
          font-size: rem(16); }
        .label {
          position: relative;
          top: -2px; } }
      &-f11 {
        font-size: rem(11);
        font-family: $font-roboto-medium; } }
    &--filter {
      border-color: #e0e0e0;
      background-color: #e0e0e0;
      margin-bottom: 0;
      &.is-focus {
        background: $color-white;
        border-color: $color-white; } }
    &--price {
      .form-control {
        height: 25px;
        width: 100px;
        text-align: center;
        padding: 0 10px;
        font-size: rem(14);
        font-family: $font-roboto-medium; } }
    &.has-feedback {
      i.ion-alert-circled {
        display: none;
        position: absolute;
        right: 15px;
        top: 8px;
        color: $color-blue;
        font-size: rem(12);
        transform: skewX(15deg);
        z-index: 10; } }
    &.has-error {
      border-color: $color-blue;
      background: $color-white;
      .form-control {
        padding-right: 50px;
        &::-webkit-input-placeholder {
          color: $color-blue; }
        &::-moz-placeholder {
          color: $color-blue; }
        &:-ms-input-placeholder {
          color: $color-blue; }
        &:-moz-placeholder {
          color: $color-blue; } }
      i.ion-alert-circled {
        display: block !important; }
      .error-text {
        display: block; }
      &--red {
        border-color: $color-red;
        .form-control {
          color: $color-red;
          &::-webkit-input-placeholder {
            color: $color-red; }
          &::-moz-placeholder {
            color: $color-red; }
          &:-ms-input-placeholder {
            color: $color-red; }
          &:-moz-placeholder {
            color: $color-red; } }
        i.ion-alert-circled {
          color: $color-red; } } }
    &--small {
      border-radius: 3px;
      .form-control, .form__control {
        height: 28px; }
      &.has-feedback {
        i.ion-alert-circled {
          top: 6px; } } }
    &--normal {
      transform: skewX(0deg);
      background: none;
      border: 0;
      margin-left: 0;
      margin-right: 0;
      .form-control, .form__control {
        transform: skewX(0deg);
        background: $color-lightgray;
        border: 1px solid $color-lightgray; } }
    &--select {
      z-index: 10; }
    .selectric-wrapper {
      margin-left: 0px;
      transform: skewX(15deg);
      &.selectric-open {
        .selectric {
          border-radius: 3px 3px 3px 0; } } }
    .selectric-items {
      margin-left: -5px;
      border: 0;
      background: $color-lightgray;
      // max-height: 230px !important
      font-family: $font-roboto;
      padding: 10px 0;
      // transform: skewX(-15deg)
      li {
        padding: 0 10px 0 20px;
        line-height: 24px;
        min-height: 24px;
        color: $primary-color;
        &:hover {
          background: none;
          color: $color-red; }
        &.selected {
          background: none;
          color: $color-red; } } }
    .selectric {
      border: 0;
      background: none;
      border-radius: 3px;
      .label {
        height: 33px;
        line-height: 33px;
        font-family: $font-roboto-medium;
        color: $primary-color;
        // transform: skewX(15deg)
        margin-left: 20px;
        margin-right: 28px; }
      .button {
        height: 33px;
        width: 28px;
        line-height: 33px;
        color: $primary-color;
        background: none;
        // transform: skewX(15deg)
        font-size: 0;
        &::after {
          display: none; }
        &::before {
          border: 0;
          content: "\f123";
          font-family: $font-ionicons;
          font-size: rem(10);
          display: block;
          line-height: 1;
          position: absolute;
          color: $primary-color;
          left: 6px;
          top: 12px;
          right: inherit;
          bottom: inherit;
          color: #535353;
          width: 0;
          font-weight: bold; } } }
    .error-text {
      display: none;
      position: absolute;
      font-size: rem(10);
      left: 0;
      top: 100%;
      transform: skewX(15deg);
      color: $color-red;
      margin-top: 2px; }
    &--search {
      input {
        padding-right: 45px; }
      button {
        float: right;
        height: 35px;
        width: 35px;
        background: none;
        border: 0;
        margin-top: -35px;
        font-size: rem(15);
        color: $color-red;
        transform: skewX(15deg); } }
    &--file {
      background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAFCAYAAACNbyblAAAAKElEQVQYV2NkQAPnz5//z4gsBhIwNDRkhAvCBECKwILIAmBBdAGQIADgRxNydB5tIgAAAABJRU5ErkJggg==');
      .inputfile {
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        overflow: hidden;
        position: absolute;
        z-index: -1; }
      .inputfile + label {
        font-size: rem(12);
        /* 20px */
        font-weight: 400;
        text-overflow: ellipsis;
        white-space: nowrap;
        cursor: pointer;
        display: block;
        overflow: hidden;
        padding: 0;
        font-family: $font-roboto-medium;
        transform: skewX(15deg);
        &:focus {
          outline: none; } }
      .inputfile-6 {
        + label {
          color: $primary-color;
          // border: 1px solid #d3394c
          // background-color: #f1e5e6
          padding: 0;
          display: block;
          margin: 0; }
        &:focus + label, &.has-focus + label {
          border-color: #722040; }
        + label {
          &:hover {
            border-color: #722040; }
          span, strong {
            padding: 0 20px;
            /* 10px 20px */ }
          span {
            width: 100%;
            height: 33px;
            line-height: 33px;
            display: block;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            vertical-align: top;
            i.ion-archive {
              position: absolute;
              right: 15px;
              color: $color-808080;
              font-size: rem(13);
              top: 50%;
              transform: translateY(-50%); } } }
        &:focus + label strong, &.has-focus + label strong, + label:hover strong {
          background-color: #722040; } } } }
  &-control, &__control {
    display: block;
    width: 100%;
    height: 34px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    background-color: $color-white;
    background-image: none;
    border: 1px solid $color-gray;
    border-radius: 4px;
    box-shadow: none;
    transition: all .35s;
    transform: skewX(15deg);
    background: none;
    height: 33px;
    border: 0;
    padding: 0 20px 0 20px;
    width: 100%;
    font-size: rem(12);
    font-family: $font-roboto-medium;
    &:focus {
      box-shadow: none; } }
  &__legend {
    text-transform: uppercase;
    font-family: $font-roboto-bold;
    margin: 0 0 20px 0;
    &--small {
      font-size: rem(18); } }
  &__fieldset {
    margin-bottom: 20px; } }

textarea {
  &.form-control, &.form__control {
    height: 180px;
    resize: none;
    padding-top: 5px;
    padding-bottom: 5px; } }

label {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: 400; }

legend {
  border: 0;
  display: block;
  line-height: 1;
  font-family: $font-roboto-medium;
  margin-top: 17px;
  color: $primary-color; }

.has {
  &-success .form-control {
    border-color: #3c763d;
    box-shadow: none; }

  &-warning .form-control {
    border-color: #8a6d3b;
    box-shadow: none; }

  &-error .form-control {
    border-color: #a94442;
    box-shadow: none;
    &:focus {
      box-shadow: none; } }

  &-success .control-label {
    color: #3c763d; }

  &-warning .control-label {
    color: #8a6d3b; }

  &-error .control-label {
    color: #a94442; } }
